<template>
  <div class="goodsManagement">
    <div class="top">
       <form action="/">
        <van-search v-model="value" show-action action-text="搜索" @cancel="onSearch" placeholder="搜索商品" background="#fff" @search="onSearch" @blur="blur" />
      </form>
      <van-tabs v-model="active" :title-active-color="'#ee0a24'" :border="false" @change="onChange">
        <van-tab v-for="(item,index) in cateList" :key="index" :title="item.cateName" />
      </van-tabs>
    </div>
    <div class="mid">
      <div class="list mescroll" id="list">
        <div id="listData">
          <div class="item" v-for="(item,index) in goodsList" :key="index">
            <van-swipe-cell>
              <div class="goods">
                <img :src="$utils.dealImg(item.gimg)" alt />
                <div class="right">
                  <div class="name">{{item.gname}}</div>
                  <div class="price">
                    <span>￥{{item.goutPrice?item.goutPrice.toFixed(2):''}}</span>
                    <del>￥{{item.gmarketPrice?item.gmarketPrice.toFixed(2):''}}</del>
                  </div>
                </div>
              </div>
              <template slot="right">
                <van-button square type="primary" text="编辑" @click="edit(item,index)" />
                <van-button square type="danger" text="删除" @click="del(item,index)" />
              </template>
            </van-swipe-cell>
          </div>
        </div>
      </div>
    </div>
    <div class="bottom" @click="add">新增商品</div>
  </div>
</template>
<script>
import {
  Tab,
  Tabs,
  Search,
  SwipeCell,
  Button,
  Popup,
  CellGroup,
  Field,
  Dialog
} from "vant";
import { getShopCateList, getShopGoodsList, delGood } from "@/api";
import MeScroll from "mescroll.js";
export default {
  components: {
    [Tabs.name]: Tabs,
    [Tab.name]: Tab,
    [Search.name]: Search,
    [SwipeCell.name]: SwipeCell,
    [Button.name]: Button,
    [Popup.name]: Popup,
    [CellGroup.name]: CellGroup,
    [Field.name]: Field
  },
  data() {
    return {
      value: "",
      active: 0,
      cateList: [],
      goodsList: [],
      mescroll: null
    };
  },
  methods: {
    blur() {
      document.body.scrollTop = 0;
    },
    onSearch() {
      this.mescroll.scrollTo(0, 0);
      this.mescroll.resetUpScroll();
    },
    onChange() {
      this.mescroll.scrollTo(0, 0);
      this.mescroll.resetUpScroll();
    },
    edit(item) {
      sessionStorage.setItem("goodDetail", JSON.stringify(item));
      this.$router.push({
        name: "addGood",
        query: {
          type: "2"
        }
      });
    },
    del(item, index) {
      let that = this;
      Dialog.confirm({
        title: "标题",
        message: "是否确认删除此商品？"
      })
        .then(() => {
          // on confirm
           // on confirm
          const loading = this.$toast.loading({
            duration: 0, // 持续展示 toast
            forbidClick: true // 禁用背景点击
          });
          delGood({ id: item.id }).then(res => {
            loading.clear();
            if (res.code == 0) {
              that.$toast("删除成功");
               that.mescroll.resetUpScroll();
            } else {
              that.$toast(res.msg);
            }
          });
        })
        .catch(() => {
          // on cancel
        });
    },
    add() {
      this.$router.push({
        name: "addGood",
        query: {
          type: "1"
        }
      });
    },
    loadCate() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      getShopCateList().then(res => {
        loading.clear();
        if (res.code == 0) {
          if (!res.data.length) {
            this.$toast("请先添加分类");
            setTimeout(() => {
              this.$router.back();
            }, 1000);
            return;
          }
          this.cateList = res.data;
          this.startMescroll();
        } else {
          this.$toast(res.msg);
        }
      });
    },
    loadData(pageNum, pageSize, successCallback, errorCallback) {
      let $this = this;
      let dataObj = {
        pageNum: pageNum,
        pageSize: pageSize,
        cateCode: this.cateList[this.active].cateCode,
        keys: this.value
      };
      getShopGoodsList(dataObj)
        .then(res => {
          if (res.code == 0) {
            successCallback && successCallback(res.data);
          } else {
            $this.$toast.fail(res.errorMsg);
          }
        })
        .catch(res => {
          errorCallback && errorCallback();
          console.log(res);
        });
    },
    startMescroll() {
      this.mescroll = new MeScroll("list", {
        //在vue的mounted生命周期初始化mescroll,确保此处配置的id能够被找到
        up: {
          callback: this.upCallback, //上拉回调
          //以下参数可删除,不配置
          isBounce: false, //此处禁止ios回弹,解析(务必认真阅读,特别是最后一点): http://www.mescroll.com/qa.html#q10
          page: { size: 10 }, //可配置每页8条数据,默认10
          empty: {
            //配置列表无任何数据的提示
            warpId: "listData",
            icon: require("@/assets/img/nodata.png"),
            tip: "暂无数据"
            //						  	btntext : "去逛逛 >" ,
            //						  	btnClick : function() {
            //						  		alert("点击了去逛逛按钮");
            //						  	}
          },
          noMoreSize: 10,
          scrollbar: { use: false },
          htmlNodata: '<p class="upwarp-nodata">-- 无更多数据 --</p>',
          lazyLoad: {
            use: true // 是否开启懒加载,默认false
          }
        }
      });
    },
    //上拉回调 page = {num:1, size:10}; num:当前页 ,默认从1开始; size:每页数据条数,默认10
    upCallback: function(page) {
      //联网加载数据
      let $this = this;
      $this.loadData(
        page.num,
        page.size,
        function(curPageData) {
          //curPageData=[]; //打开本行注释,可演示列表无任何数据empty的配置

          //如果是第一页需手动制空列表 (代替clearId和clearEmptyId的配置)
          console.log(curPageData);
          if (page.num == 1) $this.goodsList = [];
          if (curPageData.list) {
            curPageData.list.map(item => {
              $this.goodsList.push(item);
            });
          }
          //联网成功的回调,隐藏下拉刷新和上拉加载的状态;
          //mescroll会根据传的参数,自动判断列表如果无任何数据,则提示空;列表无下一页数据,则提示无更多数据;
          //   console.log("page.num="+page.num+", page.size="+page.size+", curPageData.length="+curPageData.rows.length+", $this.goodsList.length==" + $this.pdlist.length);
          console.log(page);

          //方法二(推荐): 后台接口有返回列表的总数据量 totalSize
          $this.mescroll.endBySize(
            curPageData.list ? curPageData.list.length : 0,
            curPageData.count
          ); //必传参数(当前页的数据个数, 总数据量)
        },
        function() {
          //联网失败的回调,隐藏下拉刷新和上拉加载的状态;
          $this.mescroll.endErr();
        }
      );
    }
  },
  mounted() {
    document.title = "商品管理";
    sessionStorage.removeItem("goodDetail");
    this.loadCate();
  }
};
</script>
<style lang="scss" scoped>
.goodsManagement {
  .mid {
    height: calc(100vh - 98px - 50px);
    .list {
      .item {
        background: #fff;
        border-bottom: 1pt solid #efefef;
        .goods {
          padding: 10px;
          display: flex;
          align-items: center;
          img {
            width: 70px;
            height: 70px;
            border-right: 5px;
            margin-right: 10px;
          }
          .right {
            flex: 2;
            .name {
              font-weight: bold;
              line-height: 30px;
            }
            .price {
              span {
                color: #ee0a24;
                margin-right: 5px;
              }
              del {
                color: #999;
                font-size: 13px;
              }
            }
          }
        }
        .van-button {
          height: 100%;
        }
      }
    }
  }
  .bottom {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100vw;
    height: 50px;
    line-height: 50px;
    background: $base-color;
    color: #fff;
    text-align: center;
  }
  .van-popup {
    border-radius: 5px;
  }
  .popup {
    background: #fff;
    width: 85vw;
    height: 85vh;
    position: relative;
    &-top {
      padding: 10px;
      display: flex;
      img {
        width: 70px;
        height: 70px;
        border-right: 5px;
        margin-right: 10px;
      }
      .right {
        flex: 2;
        .name {
          line-height: 20px;
          height: 40px;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          overflow: hidden;
          text-align: justify;
          word-break: break-all;
        }
        .price {
          margin: 10px 0 0;
          span {
            color: #ee0a24;
            margin-right: 5px;
          }
          del {
            color: #999;
            font-size: 13px;
          }
        }
      }
    }
    &-mid {
      .van-cell {
        padding: 10px;
      }
    }
    &-bottom {
      padding: 10px;
      position: absolute;
      width: calc(100% - 20px);
      height: 45px;
      bottom: 0;
      left: 0;
      .btn {
        width: 100%;
        line-height: 45px;
        text-align: center;
        background: #ee0a24;
        color: #fff;
        font-size: 13px;
        border-radius: 5px;
      }
    }
  }
}
</style>